import React, { useState } from "react";
import { findDOMNode } from "react-dom";

import ReactPlayer from "react-player";

import "./styles.scss";
const urls = [
  "https://tv4.live/api/stream/the.kfir@gmail.com/R4wNrCnhd6YQ4SrbJ3rNB6/livetv.epg/sport5.4k.il.m3u8",
  "https://tv4.live/api/stream/the.kfir@gmail.com/R4wNrCnhd6YQ4SrbJ3rNB6/livetv.epg/5sporthd.il.m3u8",
  "https://tv4.live/api/stream/the.kfir@gmail.com/R4wNrCnhd6YQ4SrbJ3rNB6/livetv.epg/5gold.il.m3u8",
  "https://tv4.live/api/stream/the.kfir@gmail.com/R4wNrCnhd6YQ4SrbJ3rNB6/livetv.epg/5live.il.m3u8",
  "https://tv4.live/api/stream/the.kfir@gmail.com/R4wNrCnhd6YQ4SrbJ3rNB6/livetv.epg/5plus.il.m3u8",
  "https://tv4.live/api/stream/the.kfir@gmail.com/R4wNrCnhd6YQ4SrbJ3rNB6/livetv.epg/5starts.il.m3u8"
];

export default function App() {
  const [currentVideo, setCurrentVideo] = useState(urls[0]);
  return (
    <div className="App">
      <h1>Y</h1>
      <div id="video">
        <ReactPlayer url={currentVideo} playing controls />
      </div>
      <ul>
        {urls.map((url) => {
          const name = url.substring(
            url.lastIndexOf("/") + 1,
            url.lastIndexOf(".il.m3u8")
          );
          return (
            <li key={name}>
              <a
                href="#video"
                onClick={() => {
                  setCurrentVideo(url);
                }}
              >
                {name}
              </a>
            </li>
          );
        })}
      </ul>
    </div>
  );
}
